export default {
	data() {
		return {
			searchListData: [],
      keywords: '',
      page: 0,
      pageSize: 15,
      totolPage: 1
		}
	},
  onLoad() {
    this.getSearchListData();
  },
  onPullDownRefresh() {
    this.getSearchListData();
  },
  onReachBottom() {
    var me = this;
    me.page = me.page+1;
    if( me.page > me.totolPage ) {
      return false;
    }
    me.sendSearch(1)
  },
	methods: {
    // 请求搜索页默认数据
    getSearchListData () {
      uni.showToast({
          title: '加载中',
          icon: "loading",
          mask: true,
          duration: 5000
      });
      var me = this;
      me.keywords = '';
      me.noticeHeroData = [];
      me.heroReest({
        'url': '/search/list',
        'data': {
          'keywords': '',
          'page': '',
          'pageSize': ''
        },
        success : function(res){
          if( res.data.status == 200 ){
            me.searchListData = res.data.data.rows;
          }
        },
        complete: function() {
          uni.stopPullDownRefresh()
          uni.hideToast()
        }
      })
    },
    // 发送搜索内容
    sendSearch (cancatArr) {
      uni.showToast({
          title: '加载中',
          icon: "loading",
          mask: true,
          duration: 5000
      });
      var me = this;
      if( cancatArr != 1 ){
        me.page = 1;
      }
      me.noticeHeroData = [];
      // return false;
      me.heroReest({
        'url': '/search/list',
        'data': {
          'keywords': me.keywords,
          'page': me.page,
          'pageSize': me.pageSize
        },
        success : function(res){
          if( res.data.status == 200 ){
            var resData = res.data.data.rows;
            if( cancatArr == 1 ){
              me.searchListData.push(...resData)
            } else {
              me.searchListData = resData;
            }
            me.totolPage = res.data.data.total;
          }
        },
        complete: function() {
          uni.hideToast();
        }
      })
    },
    // 跳转至详情页
    toDetailPage (id) {
      uni.navigateTo({
          url: '../detail/detail?trailerId=' + id
      });
    }
    
	}
}
